.step1:after {
  content:"Step 1";
  position: absolute;
  top: 33px;
  color: white;
  font-weight: 500;
  font-size: 1.3vw;
  left: -46px;
  -webkit-transform: rotate(270deg);
  -moz-transform: rotate(270deg);
  -o-transform: rotate(270deg);
  -ms-transform: rotate(270deg);
  transform: rotate(270deg);
  @include media(xs) {
    left: -53px;
    font-size: 5.5vw;
  }
}

